
.data {
    margin-top: 23px;

    .icon {
        font-size: 64px;
        margin-top: 19px;
        position: relative;
        bottom: 10px;
    }

    .info {
        padding-left: 11px;
        display: inline-block;
    }

    .weather-icon {
        display: inline-block;
    }

    h1 {
        margin-bottom: 5px;
    }

    @media only screen and (max-width : 480px) {
        h1 {
            font-size: 26px;
        }

        p {
            font-size: 14px;
        }

        .icon {
            font-size: 60px;
            margin-top: 16px;
        }
    }

    @media only screen and (max-width : 365px) {
        .icon {
            font-size: 32px;
        }
    }

    p {
        font-size: 21px;
        font-family: "Helvetica Neue", Arial;
    }
}

.search button {
    float: right;
}

@media only screen and (max-width : 768px) {
    .icon {
        font-size: 32px;
    }

    .search {
        margin-top: 32px;
    }

    .info {
        padding-left: 0 !important;

        h1 {
            margin-top: 0;
        }
    }
}



