
.modal {
    display: block;
    position: static;
    overflow: hidden;
    outline: 0;
    color: #3a3939 !important;
}

.modal-dialog {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
    z-index: 3;
}

@media only screen and (max-width : 768px) {
    .modal-dialog {
        margin: 0;
        width: 100%;
        padding: 0 5px;
    }
}

.modal-content {
    border-radius: 1px;
    border-color: transparent;
    box-shadow: none;
}

.option {
    margin-bottom: 15px;
}

.option-item {
    font-weight: 600;
}

.width-90 {
    width: 90px;
}



