
.forecast {
    margin-top: 60px;

    h3 {
        margin-bottom: 0;
    }

    p {
        color: #dedede;
    }

    .table {
        margin-top: 30px;

        .day {
            background: #353535;
            display: inline-block;
            border: 1px solid #424242;
            height: 260px;
            padding: 0 5px;
            margin-right: 7px;
            border-top: 8px solid #00497d;
            border-radius: 2px;
            flex-grow: 1;
            min-width: 19%;

            @media (max-width: 884px) {
                & {
                    min-width: 25%;
                    margin-bottom: 9px;
                }
            }

            .header {
                font-size: 19px;
                text-align: center;
            }

            .weather {
                text-align: center;
                font-size: 24px;
            }

            .precipitations, .humidity {
                text-align: center;
                font-size: 16px;

                i {
                    margin-right: 9px;
                }
            }

            div {
                padding: 12px 12px 0 12px;

                &.header:after, &.weather:after, &.humidity:after, &.precipitations:after {
                    content: '';
                    background-color: rgba(74, 74, 74, 0.3);
                    display: block;
                    width: 75%;
                    height: 1px;
                    margin: 10px auto 0 auto;
                }
            }

            .limits {
                text-align: center;
            }

            .min, .max {
                display: inline-block;
                padding: 5px 10px;
            }

            &.current {
                height: 280px;
                position: relative;
                bottom: 11px;
                background: #ffffff;
                color: #353535;
                box-shadow: 0 0 14px 0 rgba(255, 255, 255, 0.63);
                border-color: #e4e4e4;
                border-top-color: #ffddbb;

                .header:after,
                .weather:after,
                .humidity:after,
                .precipitations:after {
                    background-color: rgba(232, 232, 232, 0.3);
                }
            }
        }
    }

    @media only screen and (max-width : 425px) {
        & {
            margin-top: 34px;
        }

        .day {
            width: 100%;
        }
    }
}

.settings-button {
    background: transparent;
    border: 2px solid #fff;
    width: 45px;
    height: 45px;
    border-radius: 45px;
    position: relative;
    bottom: 8px;
    margin-right: 11px;

    &:hover span {
        animation: slow-rotate 4s .1s infinite;
    }

    span {
        font-size: 19px;
    }
}

@keyframes slow-rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.settings-button, .heading {
    display: inline-block;
}

.grid {
    display: flex;
    flex-wrap: wrap;
}



