
.search {
    float: right;
    margin-top: 52px;
}

.search button {
    background: transparent;
    border: 2px solid #fff;
    width: 45px;
    height: 45px;
    border-radius: 45px;

    span {
        font-size: 19px;
    }

    &:hover {
        transform: rotate(360deg);
        transition: .3s;
    }

    &:focus {
        outline: none;
    }
}

.search-box #location {
    margin: 45px 0;
    z-index: 4;
    position: relative;
    background: none;
    border: none;
    color: #fff;
    font-size: 32px;
    width: 100%;
    margin-bottom: -82px;

    @media (max-width: 650px) {
        & {
            font-size: 26px;
        }
    }

    @media (max-width: 400px) {
        & {
            font-size: 18px;
        }
    }

    &:focus {
        outline: none;
    }

    &::placeholder {
        color: #fff;
    }
}

.pac-container {
    background: #fff !important;
    border-radius: 0 !important;
    box-shadow: none !important;
    border: none !important;
    border-bottom-left-radius: 1px !important;
    border-bottom-right-radius: 1px !important;

    &:after {
        display: none !important;
    }
}

.pac-container:after {
    background-image: none !important;
    height: 0;
}

.pac-icon {
    display: none;
}

.pac-item-query {
    color: #fff;
}

.pac-item {
    background: #353535 !important;
    color: #c7c7c7;
    padding: 5px 0;
    border: none !important;

    &:hover {
        background: #383838 !important;
        cursor: pointer;
    }
}

.pac-item-query {
    padding-left: 13px;
}



